const toSlug = (str) => {
    // Chuyn ht sang ch thng
    str = str.toLowerCase();
    // xa du
    str = str
        .normalize('NFD') // chuyn chui sang unicode t hp
        .replace(/[\u0300-\u036f]/g, ''); // xa cc k t du sau khi tch t hp 
    // Thay k t 
    str = str.replace(/[]/g, 'd');
    // Xa k t c bit
    str = str.replace(/([^0-9a-z-\s])/g, '');
    // Xa khong trng thay bng k t -
    str = str.replace(/(\s+)/g, '-');
    // Xa k t - lin tip
    str = str.replace(/-+/g, '-');
    // xa phn d -  u & cui
    str = str.replace(/^-+|-+$/g, '');
    // return
    return str;
}
export default toSlug;